import '@rmwc/top-app-bar/styles';
import {SimpleTopAppBar, TopAppBarFixedAdjust} from '@rmwc/top-app-bar'
import {Drawer, DrawerHeader, DrawerTitle, DrawerSubtitle, DrawerContent} from '@rmwc/drawer'
import {List, ListItem} from '@rmwc/list'
import React from 'react';
import '@rmwc/drawer/styles';
import '@rmwc/list/styles';
function AppBar({page, setPage, toggle}){
    const [open, setOpen] = React.useState(false)
    return (
        <>
        <SimpleTopAppBar
    title={page}
    navigationIcon
    onNav={() => setOpen(true)}

  />
  <TopAppBarFixedAdjust />
  <Drawer modal open={open} onClose={() => setOpen(false)}>

        <DrawerContent>
          <List>
            <ListItem onClick={() => setPage("Schedule")}>Schedule</ListItem>
            <ListItem onClick={() => {setPage("Todos")}} >Todos</ListItem>
          </List>
        </DrawerContent>
      </Drawer>
        </>
    )
}
export default AppBar

