import {CollapsibleList, SimpleListItem} from '@rmwc/list'
import {TextField} from '@rmwc/textfield'
import mdDateTimePicker from 'md-date-time-picker'
import '@rmwc/list/styles';
import '@rmwc/textfield/styles';
import {useEffect, createRef} from 'react'

function Todos({toggle}){
    var input = createRef()
        var dialog = new mdDateTimePicker({"type": "date"})
        var time = dialog.time
    
    
    return(
        <>
    <CollapsibleList
    handle={
      <SimpleListItem
        text="Pizza"
        metaIcon="chevron_right"
      />
    }
  >
    <SimpleListItem text="Cheese" />
    <SimpleListItem text="Pepperoni" />
    <SimpleListItem text="Supreme" />
  </CollapsibleList>
  <TextField trailingIcon={{
      icon: 'event',
      onClick: () => {

                document.querySelector('.mddtp-picker__subtitle').classList.remove('mddtp-picker__color--active');
                document.querySelector('.mddtp-picker__title').classList.add('mddtp-picker__color--active')

          
          dialog.toggle(); 
          console.log(dialog.time.toString()); 
          document.querySelector('#mddtp-date__ok').addEventListener('click', () => {
              input.current.querySelector('input').value = dialog.time.format("MMM Do YY")
            })
        }
    }} ref={input}/>
    </>
  )
}
export default Todos